import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
import {Dialog} from 'vant'
export default new Vuex.Store({
	state: {
		listShow:{},
		loginCity:'',
		showImg:{},
		indexId:'',
		setName:'',
		siteName:[],
		arr:[],
		flag:false,
		affirm:'',
		appshop:''
	},
	mutations: {
		//点击订单列表中的上品跳转到确认订单
		appShop(state,item){
			state.appshop=item
			console.log(state.appshop)
			localStorage.setItem('appShop',JSON.stringify(state.appshop))
		},
		//确认订单后判断get
		appGet(state){
			if(localStorage.appShop){
				state.appshop = JSON.parse(localStorage.getItem('appShop'))
			}
		},
		//添加地址到确认订单
		routAff(state,obj){
			state.affirm = obj
			localStorage.setItem('affirm',JSON.stringify(state.affirm))
		},
		fnAdd(state,val){
			state.arr[val].num++;
			localStorage.setItem('addShop',JSON.stringify(state.arr))
		},
		fnPrev(state,val){
			state.arr[val].num--;
			if(state.arr[val].num<1){
				state.arr.splice(val,1)
			}
			if(state.arr.length<=0){
				state.flag=false
			}
			localStorage.setItem('addShop',JSON.stringify(state.arr))
		},
		//判断affirm订单地址
		getroutAff(state){
			if(localStorage.affirm){
				state.affirm = JSON.parse(localStorage.getItem('affirm'))
			}
		},
		//点击加入购物车
		addShop(state,val){
			let bool = true
			state.arr.map((item,index) =>{
				if(item._id==val._id){
					bool=false
					item.num++
				}
			})
			if(bool==true){
				val.num=1;
				state.arr.push(val)
			}
			localStorage.setItem('addShop',JSON.stringify(state.arr))
		},
		//清空购物车
		del(state){
			state.arr=[]
			state.flag=false
			localStorage.setItem('addShop',JSON.stringify(state.arr))
		},
		//显示购物车
		show(state){
			state.flag=!state.flag
		},
		//删除新增地址列表
		delSit(state,index){
			state.siteName.splice(index,1)
			localStorage.setItem('Sitn',JSON.stringify(state.siteName))
		},
		//购物车添加
		getShop(state){
			if(localStorage.addShop){
				state.arr =JSON.parse(localStorage.getItem('addShop'))
			}
		},
		//新增地址提交数据到site页面渲染
		getst(state,obj){
			state.siteName.unshift(obj)
			localStorage.setItem('Sitn',JSON.stringify(state.siteName))
		},
		//新增地址存储
		seTst(state){
			if(localStorage.Sitn){
				state.siteName =JSON.parse(localStorage.getItem('Sitn'))
			}
		},
		//推出登录删除当前本地存储
		delElem(state){
			Dialog.confirm({
				title: '标题',
				message: '退出登录'
			}).then(() => {
				// on confirm
				window.location.href="/site";
			}).catch(() => {
				// on cancel
			});
			localStorage.removeItem('Eleme')
		},
		//获取城市ID
		getId(state,val){
			state.indexId = val
			console.log(val)
			localStorage.setItem('Id',JSON.stringify(state.indexId))
		},
		//获取添加地址搜索的name
		addName(state,val){
			state.setName =val.name
		},
		//获取login穿过来的值
		logIn(state,obj){
			state.loginCity=obj
			console.log(obj)
		},
		//登录时存储信息是否登录
		getData(state,obj){
			state.listShow=obj
			localStorage.setItem('Eleme',JSON.stringify(state.listShow))
		},
		upLoad(state,val){
			state.listShow=val
			localStorage.setItem('Eleme',JSON.stringify(state.listShow))
			// state.commit('getData')
		},
		getid1(state){
			if(localStorage.getItem('Id')){
				state.indexId= JSON.parse(localStorage.getItem('Id'))
			}
		},
		getlocalStorage(state){
			if(localStorage.getItem('Eleme')){
				state.listShow = JSON.parse(localStorage.getItem('Eleme'))
			}
		},
	},
	getters:{},
	actions: {},
	modules: {}
})
