import '../plugins/axios'

export default {
	/**
	 * @method getHomeData
	 * @param {Object} option
	 * @param {String} option.tab  -可选项：all全部，good精华，ask问答，share分享，job招聘，dev测试
	 * @param {Number} option.page -页码
	 * @param {Number} option.limit -每页数量
	 * @return {Promise} data  -获取到的首页的数据对象,通过Promise.then()方法获取数据
	 * */
	async getHomeData(option) {
		let {data} = await axios.get('topics', {
			params: option
		});
		return data;
	}
}